.App {
  text-align: center;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.movie-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  flex-wrap: wrap;
}
.Movie-wrapper{
  width: 500px;
  background-color: antiquewhite;
  color: black;
}
.Movie-search{
  height: 40px;
  font-size: 20px;
  margin-bottom: 30px;
  padding: 15px;
  font-weight: bold;
  border-radius: 15px;
  width: 475px;
}
.Movie-title{
  height :55px;
  margin-bottom: 46px;
  font-weight: bold;

}

.Movie-date{
  font-style: italics;
}

.Movie-rating{
  color: green;
  margin-bottom: 24px;
  font-weight: bold;
  font-size: 1.2em;
  /* text-decoration: underline; */
}